import React, { Component } from "react";
import BinCardService from "../services/BinCardService";
import InventoryService from "../services/InventoryService";
import IssueLogService from "../services/IssueLogService";

class UpdateInventory extends Component {
  constructor(props) {
    super(props);

    this.state = {
      id: this.props.match.params.id,
      date: "",
      warehouseName: "",
      itemDescription: "",
      unit: "",
      voucherOrRefNumber: "",
      receivedFrom: "",
      issuedTo: "",
      batchNo: "",
      expiryDate: "",
      quantityReceived: "",
      quantityIssued: "",
      stockBalance: "",
      openingBalance: "",
      closingStock: "",
      losses: "",
      positiveAdjustment: "",
      negativeAdjustment: "",
      issuedToEmail: "",
      phone: "",
      dispatchedLocation: "",
      loading: false,
    };

    this.changeIssuedToHandler = this.changeIssuedToHandler.bind(this);
    this.changeIssuedQuantityHandler = this.changeIssuedQuantityHandler.bind(this);
    this.handleDispatchedLocation = this.handleDispatchedLocation.bind(this);
    this.changeIssuedToEmailHandler = this.changeIssuedToEmailHandler.bind(this);
    this.changePhonelHandler = this.changePhonelHandler.bind(this);
    this.changeVoucherHandler = this.changeVoucherHandler.bind(this);

    this.createIssueLog = this.createIssueLog.bind(this)
  }

  componentDidMount() {
    InventoryService.getInventoryById(this.state.id).then((res) => {
      let inventory = res.data;
      this.setState({
        warehouseName: inventory.warehouseName,
        batchNo: inventory.batchNo,
        unit: inventory.unit,
        itemDescription: inventory.itemDescription,
        expiryDate: inventory.expiryDate,
        openingBalance: inventory.openingBalance,
        closingStock: inventory.closingStock,
        quantityReceived: inventory.quantityReceived,
        voucherOrRefNumber: inventory.voucherOrRefNumber,
        losses: inventory.losses,
        positiveAdjustment: inventory.positiveAdjustment,
        negativeAdjustment: inventory.negativeAdjustment,
        stockBalance: inventory.stockBalance

      });
    });
  }

  formatDate(dateString) {
    const date = new Date(dateString);
    const day = date.getDate();
    const month = date.getMonth() + 1;
    const year = date.getFullYear();
    return `${day < 10 ? '0' + day : day}/${month < 10 ? '0' + month : month}/${year}`;
  }

  createIssueLog = (e) => {
    e.preventDefault();
    this.setState({ loading: true })
    const formattedDate = this.formatDate(new Date());

    let issuelog = {
      userId: JSON.parse(localStorage.getItem('user'))?.id,
      receivedFrom: JSON.parse(localStorage.getItem('user')).firstname + " " + JSON.parse(localStorage.getItem('user')).lastname,
      // date: new Date().toLocaleString().slice(0, 10),
      date: formattedDate,
      warehouseName: this.state.warehouseName,
      itemDescription: this.state.itemDescription,
      voucherOrRefNumber: this.state.voucherOrRefNumber,
      batchNo: this.state.batchNo,
      expiryDate: this.state.expiryDate,
      openingBalance: this.state.openingBalance,
      issuedTo: this.state.issuedTo,
      issuedToEmail: this.state.issuedToEmail,
      unit: this.state.unit,
      phone: this.state.phone,
      quantityIssued: this.state.quantityIssued,
      dispatchedLocation: this.state.dispatchedLocation,
      stockBalance: this.state.stockBalance,
      losses: this.state.losses,
      quantityReceived: this.state.quantityReceived,
      closingStock: this.state.closingStock,
      positiveAdjustment: this.state.positiveAdjustment,
      negativeAdjustment: this.state.negativeAdjustment,
      inventoryId: this.state.id
    };

    if (this.state.issuedTo) {
      const { openingBalance, quantityIssued } = issuelog;
      const remainingBalance = openingBalance - quantityIssued;
      if (remainingBalance >= 0) { 
        if (this.state.quantityIssued) {
          if (this.state.dispatchedLocation) {
            if (this.state.issuedToEmail) {
              // Email validation
          const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
          if (emailRegex.test(this.state.issuedToEmail)) {
              if (this.state.phone.length == 11) {
                if (this.state.phone.startsWith("090") || this.state.phone.startsWith("080") || this.state.phone.startsWith("070") || this.state.phone.startsWith("081")) {
                  if (this.state.phone) {
                    if (this.state.quantityIssued >= 0) {
                      IssueLogService.createIssueLog(issuelog).then((res) => {
                        this.props.history.push("/issue-logs");
                      });
                    } else {
                      alert("Issued Quantity must be a greater than zero!")
                      this.setState({ loading: false })
                    }

                  } else {
                    alert("Please enter phone number!")
                    this.setState({ loading: false })
                  }
                } else {
                  alert("Please a valid  phone number!")
                  this.setState({ loading: false })
                }
              } else {
                alert("Phone number is too long or too short!")
                this.setState({ loading: false })
              }
            } else {
              alert("Enter a vaild email!")
              this.setState({ loading: false })
            }
            } else {
              alert("Enter email of the receiver!")
              this.setState({ loading: false })
            }
          } else {
            alert("Please enter dispatched location!")
            this.setState({ loading: false })
          }

        } else {
          alert("Please enter issued quantity!")
          this.setState({ loading: false })
        }
      } else {
        alert("Quantity Issued cannot be greater than balance!")
        this.setState({ loading: false })
      }

    } else {
      alert("Please enter issued to!")
      this.setState({ loading: false })
    }
  };

  changeIssuedToHandler = (event) => {
    this.setState({ issuedTo: event.target.value });
  };

  changeIssuedQuantityHandler = (event) => {
    this.setState({ quantityIssued: event.target.value });
  };

  changeIssuedToEmailHandler = (event) => {
    this.setState({ issuedToEmail: event.target.value });
  };



  handleDispatchedLocation = (event) => {
    this.setState({ dispatchedLocation: event.target.value });
  };



  changeVoucherHandler = (event) => {
    this.setState({ voucherOrRefNumber: event.target.value });
  };

  changePhonelHandler = (event) => {
    this.setState({ phone: event.target.value });
  };

  cancel() {
    this.props.history.push("/inventories");
  }


  render() {

    return (

      <React.Fragment>
        <div className="container" style={{ marginTop: "15px" }}>
          <div className="row">
            <div className="card col-md-8 offset-md-2 offset-md-2">
              <h3 className="text-center" style={{ marginTop: "25px", fontWeight: "bold" }}>Create issue log</h3>
              <div className="card-body">
                <form>
                  <div className="container">
                    <div className="form-group">
                      <label style={{ marginLeft: "15px", fontWeight: "bold", fontSize: "15px" }}>Warehouse Name</label>
                      <div className="col-sm-12">
                        <input placeholder="Warehouse Name" name="warehouseName" className="form-control" value={this.state.warehouseName} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Voucher or Ref Number</label>
                      <div className="col-sm-12">
                        <input name="voucherOrRefNumber" className="form-control" value={this.state.voucherOrRefNumber} onChange={this.changeVoucherHandler} />
                      </div>


                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Description </label>
                      <div className="col-sm-12">
                        <input placeholder="Description" name="description" className="form-control" value={this.state.itemDescription} />
                      </div>


                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Batch No</label>
                      <div className="col-sm-12">
                        <input name="batchNo" className="form-control" value={this.state.batchNo} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Unit</label>
                      <div className="col-sm-12">
                        <input name="quantityReceived" className="form-control" value={this.state.unit} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Expiry Date</label>
                      <div className="col-sm-12">
                        <input name="expiryDate" className="form-control" value={this.state.expiryDate} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Opening Balance</label>
                      <div className="col-sm-12">
                        <input name="openingBalance" className="form-control" value={this.state.openingBalance} />
                      </div>

                      {/* <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Closing Stock</label>
                      <div className="col-sm-12">
                        <input name="closingStock" className="form-control" value={this.state.closingStock} />
                      </div> */}
                      {/* <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Losses</label>
                      <div className="col-sm-12">
                        <input name="phone" className="form-control" type="number" value={this.state.losses} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Positve Adjustment</label>
                      <div className="col-sm-12">
                        <input name="positiveAdjustment" className="form-control" value={this.state.positiveAdjustment} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Negative Adjustment</label>
                      <div className="col-sm-12">
                        <input name="negativeAdjustment" className="form-control" value={this.state.negativeAdjustment} />
                      </div> */}

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Quantity issued</label>
                      <div className="col-sm-12">
                        <input name="issuedQuantity" className="form-control" type="number" value={this.state.quantityIssued} onChange={this.changeIssuedQuantityHandler} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Issued To</label>
                      <div className="col-sm-12">
                        <input placeholder="IssuedTo" name="issuedTo" className="form-control" value={this.state.issuedTo} onChange={this.changeIssuedToHandler} />
                      </div>



                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Issued to email</label>
                      <div className="col-sm-12">
                        <input name="issuedToEmail" className="form-control" type="email" value={this.state.issuedToEmail} onChange={this.changeIssuedToEmailHandler} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Phone Number</label>
                      <div className="col-sm-12">
                        <input name="phone" className="form-control" type="number" value={this.state.phone} onChange={this.changePhonelHandler} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Dispatched Location</label>
                      <div className="col-sm-12">
                        <input name="Dispatched Location" className="form-control" value={this.state.dispatchedLocation} onChange={this.handleDispatchedLocation} />
                      </div>
                    </div>

                    <div className="form-row text-center" style={{ marginTop: "12px" }} >
                      <div className="col-12">
                        <button className="btn btn-success" onClick={this.createIssueLog} >
                          {this.state.loading && <div className="spinner-border text-light" role="status"></div>}
                          Create issue log </button>
                        <button className="btn btn-outline-danger" onClick={this.cancel.bind(this)} style={{ margin: "22px" }}>Cancel</button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
export default UpdateInventory;
