import React, { Component } from "react";
import '../App.css'
import { Link } from 'react-router-dom';
import { Visibility, AccountBalanceOutlined, Search } from '@material-ui/icons'
import { CSVLink } from "react-csv";
import axios from 'axios'
import { BaseURL } from "../services";
import jsPDF from 'jspdf';
import 'jspdf-autotable';



const headers = [
  // { label: "Id", key: "id" },
  { label: "Date", key: "date" },
  { label: "Warehouse name", key: "warehouseName" },
  { label: "Item description", key: "itemDescription" },
  // { label: "Unit Of Measure", key: "unit" },
  { label: "Voucher/Ref.number", key: "voucherOrRefNumber" },
  { label: "Received from", key: "receivedFrom" },
  { label: "Issued to", key: "issuedTo" },
  { label: "Batch/Lot/Serial", key: "batchNo" },
  { label: "Expiry date", key: "expiryDate" },
  { label: "Quantity Received", key: "quantityReceived" },
  { label: "Quantity issued", key: "quantityIssued" },
  // { label: "Losses", key: "losses" },
  // { label: "Positive Adjustment", key: "positiveAdjustment" },
  // { label: "Negative Adjustment", key: "negativeAdjustment" },
  { label: "IssuedTo Email", key: "issuedToEmail" },
  { label: "Phone", key: "phone" },
  { label: "DispatchedLocation", key: "dispatchedLocation" },
  { label: "Stock Balance", key: "stockBalance" },

];

class IssueLogList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: JSON.parse(localStorage.getItem('user')).password,
      states: JSON.parse(localStorage.getItem('user')).states,
      userType: JSON.parse(localStorage.getItem('user')).userType,
      issuelogs: [],
      currentPage: 1,
      recordPerPage: 100,
      search: '',
      startDate: '',
      endDate: '',
      searchResults: [],
      // searchDates: { startDate: '', endDate: '' },
      loading: false
    };

    this.csvLinkEl = React.createRef();
    this.form = React.createRef();

  }

  componentDidMount() {
    this.getIssueLogsByStatesPagination(this.state.currentPage);

  }


  getIssueLogsByStatesPagination(currentPage) {
    currentPage = currentPage - 1;
    axios.get(`${BaseURL}/issuelogs/${this.state.userType === 'User' ? this.state.states : ''}?page=${currentPage}&size=${this.state.recordPerPage}`)
      .then(response => response.data).then((data) => {
        this.setState({
          issuelogs: data.content,
          totalPages: data.totalPages,
          totalElements: data.totalElements,
          currentPage: data.number + 1
        });
      });
  }


  //Writing All the pagination functions
  //Show Next page
  showNextPage = () => {
    if (this.state.currentPage < Math.ceil(this.state.totalElements / this.state.recordPerPage)) {
      if (!this.state.search) {
        this.getIssueLogsByStatesPagination(this.state.currentPage + 1);
      } else {
        this.searchBincard(this.state.currentPage + 1)
      }
    }
  };

  //Show Last Page
  showLastPage = () => {
    if (this.state.currentPage < Math.ceil(this.state.totalElements / this.state.recordPerPage)) {
      if (!this.state.search.search) {
        this.getIssueLogsByStatesPagination(Math.ceil(this.state.totalElements / this.state.recordPerPage));
      }
      else {
        this.searchBincard(Math.ceil(this.state.totalElements / this.state.recordPerPage));
      }
    }
  };

  //Show First page
  showFirstPage = () => {
    let firstPage = 1;
    if (this.state.currentPage > firstPage) {
      if (!this.state.search) {
        this.getIssueLogsByStatesPagination(firstPage);
      } else {
        this.searchBincard(firstPage)
      }
    }
  };

  //Show previous page
  showPrevPage = () => {
    let prevPage = 1
    if (this.state.currentPage > prevPage) {
      if (!this.state.search) {
        this.getIssueLogsByStatesPagination(this.state.currentPage - prevPage);
      } else {
        this.searchBincard(this.state.currentPage - prevPage);
      }
    }
  };

  //Search Box Method
  searchBox = (e, name) => {
    const { value } = e.target

    if (name !== 'search') {
      this.setState({
        searchDates: {
          ...  this.state.searchDates,
          [name]: value
        }
        //assigning value to event target
        // [e.target.name]: e.target.value,
      });
    }
    else {
      this.setState({
        [name]: value
      });
    }

  };


  //Search Method Logic
  searchBincard = (currentPage) => {
    currentPage = currentPage - 1;
    axios.get(`${BaseURL}/issuelogs/` + this.state.search + "?page=" + currentPage + "&size=" + this.state.recordPerPage)
      .then(response => response.data).then((data) => {
        this.setState({
          issueLogs: data.content,
          totalPages: data.totalPages,
          totalElements: data.totalElements,
          currentPage: data.number + 1
        });
      });
  };

  //Reset Search Box
  resetBincard = (currentPage) => {
    this.setState({ "search": '' });
    this.getIssueLogsByStatesPagination(currentPage);
  };


  handleStartDateChange = event => {
    this.setState({ startDate: event.target.value });
  };

  handleEndDateChange = event => {
    this.setState({ endDate: event.target.value });
  };

  handleSearch = (data) => {
    this.setState({
      searchResults: data,
    });
  };


  // CRS-Bvnr-bSj9-rVGrW
  editBincard(id) {
    this.props.history.push(`/update-bin/${id}`);
  }


  cancel() {
    this.props.history.push("/dashboard");
  }

  upload() {
    this.props.history.push("/upload-asset");
  }

  handleSearch = () => {
    const { startDate, endDate, currentPage, recordPerPage } = this.state;
    const formattedStartDate = this.formatDate(startDate);
    const formattedEndDate = this.formatDate(endDate);

    if (endDate < startDate) {
      alert("End date cannot be less than start date");
    } else {

      axios.get(`${BaseURL}/search?startDate=${formattedStartDate}&endDate=${formattedEndDate}&page=${currentPage}&size=${recordPerPage}`)
        .then(response => {
          const data = response.data;
          this.setState({
            issuelogs: data.content,
            totalPages: data.totalPages,
            totalElements: data.totalElements,
            currentPage: data.number + 1
          });
        })
        .catch(error => {
          console.error('Error:', error);
        });
    }
  };


  formatDate = (date) => {
    const formattedDate = new Date(date).toLocaleDateString('en-GB', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    });
    return formattedDate;
  };

  // handleExport = () => {
  //   const { startDate, endDate, currentPage } = this.state;
  //   const formattedStartDate = this.formatDate(startDate);
  //   const formattedEndDate = this.formatDate(endDate);
  //   axios.get(`http://localhost:8080/api/v1/exports?startDate=${formattedStartDate}&endDate=${formattedEndDate}&page=${currentPage}&size=${this.state.recordPerPage}`)
  //     .then(response => {
  //       const csvData = response.data;

  //       const csvFile = new Blob([csvData], { type: 'text/csv' });
  //       const csvURL = URL.createObjectURL(csvFile);
  //       const tempLink = document.createElement('a');
  //       tempLink.href = csvURL;
  //       tempLink.setAttribute('download', 'search_results.csv');
  //       tempLink.click();
  //     })
  //     .catch(error => {
  //       console.error('Error:', error);
  //     });
  // };



  handleExport = () => {
    const { startDate, endDate, currentPage, recordPerPage } = this.state;
    const formattedStartDate = this.formatDate(startDate);
    const formattedEndDate = this.formatDate(endDate);

    const params = new URLSearchParams();
    params.append('startDate', formattedStartDate);
    params.append('endDate', formattedEndDate);
    params.append('page', currentPage);
    params.append('size', recordPerPage);

    axios
      .get(`http://localhost:8080/api/v1/exports?${params.toString()}`)
      .then(response => {
        const csvData = response.data;
        const blob = new Blob([csvData], { type: 'text/csv' });
        const csvURL = URL.createObjectURL(blob);
        const tempLink = document.createElement('a');
        tempLink.href = csvURL;
        tempLink.setAttribute('download', 'search_results.csv');
        tempLink.click();
      })
      .catch(error => {
        console.error('Error:', error);
      });
  };

  handlePageChange = page => {
    this.setState({ currentPage: page }, () => {
      this.handleSearch();
    });
  };


  handleExportToPDF = () => {
    const { startDate, endDate, currentPage, recordPerPage } = this.state;
    const formattedStartDate = this.formatDate(startDate);
    const formattedEndDate = this.formatDate(endDate);

    const params = new URLSearchParams();
    params.append('startDate', formattedStartDate);
    params.append('endDate', formattedEndDate);
    params.append('page', currentPage);
    params.append('size', recordPerPage);

    axios
      .get(`http://localhost:8080/api/v1/exports/pdf?${params.toString()}`)
      .then(response => {
        const data = response.data;

        if (data.length === 0) {
          console.log('No data available.');
          return;
        }

        const formattedData = data.map(issuelog => {
          return [
            issuelog.date,
            issuelog.warehouseName,
            issuelog.itemDescription,
            issuelog.unit,
            issuelog.voucherOrRefNumber,
            issuelog.receivedFrom,
            issuelog.issuedTo,
            issuelog.batchNo,
            issuelog.expiryDate,
            issuelog.quantityReceived,
            issuelog.quantityIssued,
            issuelog.issuedToEmail,
            issuelog.phone,
            issuelog.dispatchedLocation,
            issuelog.stockBalance,
          ];
        });

        const pdf = new jsPDF();
        pdf.setFontSize(12);
        pdf.text(10, 10, 'Search Results');
        pdf.setFontSize(10);
        pdf.text(10, 20, 'Start Date: ' + formattedStartDate);
        pdf.text(10, 30, 'End Date: ' + formattedEndDate);
        pdf.text(10, 40, 'Page: ' + currentPage);
        pdf.text(10, 50, 'Records per Page: ' + recordPerPage);

        pdf.autoTable({
          head: [headers.map(header => header.label)],
          body: formattedData,
          startY: 60,
        });

        pdf.save('search_results.pdf');
      })
      .catch(error => {
        console.error('Error:', error);
      });
  };


  render() {
    const { issuelogs, currentPage, totalPages, recordPerPage, startDate, endDate } = this.state;
    const user = JSON.parse(localStorage.getItem('user'));
    const userType = user?.userType;
    const userLocation = user?.result?.states
    const userIssueLogs = this.state?.issuelogs?.map((x) => x).filter((x) => x.states === userLocation)
    // const data = userType !== 'User' ? this.state.assets : userAssets;
    const data = userType !== 'User' ? this.state.issuelogs : userIssueLogs;



    return (
      <React.Fragment>
        <div className="asset-list">
          {/* <Topbar /> */}
          {/* <div className="row"> */}
          <div className="top" style={{ backgroundColor: "#008000" }}>
            <div style={{ marginTop: "20px" }} >

              <button style={{ borderRadius: "6px", margin: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" data-bs-placement="top" title="Home" onClick={this.cancel.bind(this)}>
                <AccountBalanceOutlined />
              </button>
            </div>
            <div className="d-flex flex-row bd-highlight mb-3">
              <div style={{ margin: "20px" }}>
                <span className="logs">ISSUE LOGS</span>
              </div>
              {/* <input style={{ borderRadius: "12px", marginTop: "20px", marginRight: "15px", marginLeft: "40px" }} type="text" className="form-control" name="search" size="60" placeholder="Search by PO Number/Issued To/Dispatched Location/Description" data-bs-placement="top" title="Search" value={search} onChange={(e) => this.searchBox(e, 'search')} />
              <button style={{ borderRadius: "12px", marginTop: "15px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} type="button" name="search" className=" btn btn-outline-primary" onClick={this.searchBincard}><SearchOutlined /></button> */}
            </div>

            <div className="topRight">
              <span style={{ backgroundColor: "#82E0AA", color: "#FFFFFFF", margin: "6px" }}>Start Date</span>
              <input style={{ borderRadius: "6px", marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#000000" }} className="btn btn-primary float-lg-end" type="date" value={startDate} onChange={e => this.setState({ startDate: e.target.value })} />
              <span style={{ color: "#FFFFFFF", backgroundColor: "#82E0AA", margin: "6px" }}>End Date</span>
              <input style={{ borderRadius: "6px", marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#000000" }} className="btn btn-primary float-lg-end" type="date" value={endDate} onChange={e => this.setState({ endDate: e.target.value })} />
              <button style={{ borderRadius: "6px", marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#000000" }} className="btn btn-primary float-lg-end" data-bs-placement="top" title="search" onClick={this.handleSearch}> <Search /></button>
              <div>

              </div>
              {/* {
                users !== 'User' &&
                <button style={{ marginRight: "8px", margin: "10px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" onClick={this.createAsset.bind(this)} >
                  <AddCircleOutlineSharp />
                </button>
              } */}
              {/* {
                users !== 'User' &&
                <button style={{ marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" onClick={this.upload.bind(this)}>
                  <CloudUploadOutlined />
                </button>} */}
              {/* 
                <input placeholder="start date" type="date" name="date" className="form-control" value={searchDates.startDate} onChange={(e)=>this.searchBox(e, 'startDate')} />
                <input placeholder="end date" type="date" name="date" className="form-control" value={searchDates.endDate} onChange={(e)=>this.searchBox(e, 'endDate')}  /> */}


              {/* {
                users !== 'User' &&
                <button style={{ marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" data-bs-placement="top" title="Download" onClick={downloadReport}>
                  <CloudDownloadOutlined />
                </button>
              } */}
            </div>
            {/* <div>
        <label>Start Date:</label>
        <input type="date" value={startDate} onChange={this.handleStartDateChange} />

        <label>End Date:</label>
        <input type="date" value={endDate} onChange={this.handleEndDateChange} />

        <button onClick={this.fetchData}>Fetch and Download Data</button>
      </div> */}

          </div>
          <table className="table table-striped table-bordered">
            <thead style={{ textAlign: "center", fontSize: "12px" }}>
              <tr>
                <th>No.</th>
                <th>Date</th>
                <th>Warehouse name</th>
                <th>Item Description</th>
                {/* <th>Unit</th> */}
                <th>Voucher or refNumber</th>
                <th>Received from</th>
                <th>Issued to</th>
                <th>Batch no</th>
                <th>Expiry date</th>
                <th>Quantity received</th>
                <th>Quantity issued</th>
                <th>Stock balance</th>
                {/* <th>Losses</th>
                <th>Positive adjustment</th>
                <th>Negative adjustment</th> */}
                <th>Issuedto email</th>
                <th>Phone number</th>
                <th>Dispatched location</th>
                <th colSpan="4">Actions</th>
              </tr>
            </thead>
            <tbody style={{ textAlign: "center", fontSize: "11px" }}>
              {issuelogs.length === 0 ?
                <tr align="center"><td colSpan="24">No Record Found</td></tr> :
                data?.map((issuelog, index) => (
                  <tr key={issuelog?.id}>
                    <td>
                      {(recordPerPage * (currentPage - 1)) + index + 1}</td>
                    <td>{issuelog.date}</td>
                    <td>{issuelog.warehouseName}</td>
                    <td>{issuelog.itemDescription}</td>
                    {/* <td>{issuelog.unit}</td> */}
                    <td>{issuelog.voucherOrRefNumber}</td>
                    <td>{issuelog.receivedFrom}</td>
                    <td>{issuelog.issuedTo}</td>
                    <td>{issuelog.batchNo}</td>
                    <td>{issuelog.expiryDate}</td>
                    <td>{issuelog.quantityReceived}</td>
                    <td>{issuelog.quantityIssued}</td>
                    <td>{issuelog.stockBalance}</td>
                    {/* <td>{issuelog.losses}</td>
                    <td>{issuelog.positiveAdjustment}</td>
                    <td>{issuelog.negativeAdjustment}</td> */}
                    <td>{issuelog.issuedToEmail}</td>
                    <td>{issuelog.phone}</td>
                    <td>{issuelog.dispatchedLocation}</td>


                    {/* <td className="text-center"><Link to={`/update-asset/${bincard.id}`} className="edit"><Edit /></Link></td> */}


                    {/* <td className="text-center" style={{ paddingLeft: "20px" }}><i onClick={() => this.deleteAsset(asset.id)} className="fa fa-trash" style={{ color: "red" }} ><Delete /> </i></td> */}

                    <td className="text-center" style={{ paddingLeft: "20px" }}><Link to={`/view-issueLog/${issuelog.id} `} className="view" style={{ alignItem: "center", color: "green" }}> <Visibility /></Link> </td>
                  </tr>
                ))}
            </tbody>
          </table>
          <table className="table">
            <div style={{ float: 'left', fontFamily: 'monospace', color: '#0275d8' }}>
              Page {currentPage} of {totalPages}
            </div>
            <div style={{ float: 'right' }}>
              <div className="clearfix"></div>
              <nav aria-label="Page navigation example">
                <ul className="pagination">
                  <li className="page-item"><a type="button" className="page-link" disabled={currentPage === 1 ? true : false} onClick={this.showPrevPage}>Previous</a></li>
                  <li className="page-item"><a type="button" className="page-link" disabled={currentPage === 1 ? true : false} onClick={this.showFirstPage}>First</a></li>
                  <li className="page-item"><a type="button" className="page-link" disabled={currentPage === totalPages ? true : false} onClick={this.showNextPage}>Next</a></li>
                  <li className="page-item"><a type="button" className="page-link" disabled={currentPage === totalPages ? true : false} onClick={this.showLastPage}>Last</a></li>
                </ul>
              </nav>
            </div>
          </table>
          <button style={{ borderRadius: "6px", marginBottom: "28px", marginRight: "25px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#000000" }} className="btn btn-primary float-lg-end" data-bs-placement="top" title="export by dates" onClick={this.handleExport}>Export to CSV</button>

          <div>

            {/* <button onClick={this.handleExportToPDF}>Export to PDF</button> */}
          </div>
          {/* <button style={{ borderRadius: "6px", margin: "28px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#000000" }} className="btn btn-primary float-lg-end" data-bs-placement="top" title="export by dates" onClick={this.handleExport}>Export to CSV</button> */}
          <CSVLink
            headers={headers}
            data={data}
            totalPages={totalPages}
            filename="bincard.csv"
            target="_blank"
            ref={this.csvLinkEl} />
        </div>
      </React.Fragment>
    )
  }
}
export default IssueLogList;