import React, { useEffect, useRef } from 'react';
import { useParams, useHistory } from 'react-router-dom';
import axios from 'axios';
import { BaseURL } from '../services/index';
import { useState } from 'react';
import { CSVLink } from "react-csv";
import '../App.css';
import { CloudDownloadOutlined, ArrowBackSharp } from '@material-ui/icons';

const headers = [
  { label: "Date", key: "date" },
  { label: "Warehouse name", key: "warehouseName" },
  // { label: "Item description", key: "itemDescription" },
  // { label: "Unit Of Measure", key: "unit" },
  { label: "Voucher/Ref.number", key: "voucherOrRefNumber" },
  { label: "Received from", key: "receivedFrom" },
  { label: "Issued to", key: "issuedTo" },
  { label: "Batch/Lot/Serial", key: "batchNo" },
  { label: "Expiry date", key: "expiryDate" },
  { label: "Quantity Received", key: "quantityReceived" },
  { label: "Quantity issued", key: "quantityIssued" },
  { label: "Losses", key: "losses" },
  { label: "Positive Adjustment", key: "positiveAdjustment" },
  { label: "Negative Adjustment", key: "negativeAdjustment" },
  // { label: "IssuedTo Email", key: "issuedToEmail" },
  // { label: "Phone", key: "phone" },
  // { label: "DispatchedLocation", key: "dispatchedLocation" },
  { label: "Closing Stock", key: "stockBalance" },
];

const Bin = () => {
  const { inventoryId } = useParams();
  const [data, setData] = useState([]);

  const csvLinkEl = useRef(null);
  const form = useRef(null);
  const history = useHistory();

  useEffect(() => {
    if (!inventoryId) return;
    console.log(inventoryId);
    axios.get(`${BaseURL}/issuelog/inventory/${inventoryId}`)
      .then(response => {
        const data = response.data;
        setData(data);
      })
      .catch(error => {
        console.error('Error fetching data:', error);
      });
  }, [inventoryId]);

  const downloadReport = () => {
    csvLinkEl.current.link.click();
  };

  const goBack = () => {
    history.push("/inventories");
  };
  return (
    <div>
      <div className="top" style={{ backgroundColor: "#008000" }}>
        <div style={{ margin: "20px" }}>
          <span className="logs">Bin Card </span>
        </div>
        <div className="d-flex flex-row bd-highlight mb-6">
          <div style={{ margin: "20px" }}>
            <span className="logs">BIN CARD</span>
          </div>
        </div>

        <div className="topRight">

          <button
            style={{ marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }}
            className="btn btn-primary float-lg-end"
            onClick={goBack}
            data-bs-toggle="tooltip"
            data-bs-placement="top"
            title="Back"
          >
            <ArrowBackSharp />
          </button>
          <button
            style={{ marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }}
            className="btn btn-primary float-lg-end"
            data-bs-placement="top"
            title="Download"
            onClick={downloadReport}>
            <CloudDownloadOutlined />
          </button>
        </div>
      </div>

      <table className="bin-table">
        <thead>
          <tr>
            {/* <th>No.</th> */}
            <th>Date</th>
            <th>Warehouse Name</th>
            {/* <th>Item Description</th>
            <th>Unit of Measure</th> */}

            <th>Voucher Or Ref Number</th>
            <th>Received From</th>
            <th>Issued To</th>
            <th>Batch No.</th>
            <th>Expiry Date</th>
            <th>Quantity Received</th>
            <th>Quantity Issued</th>

            {/* <th>Losses and Adjustments</th> */}
            <th>Losses</th>
            <th>Positive Adjustments</th>
            <th>Negative Adjustments</th>
            <th>Closing Stock</th>
          </tr>
        </thead>
        <tbody>
          {data.length === 0 ?
            <tr align="center"><td colSpan="14">No Record Found</td></tr> :
            data.map((issuelog, index) => (
              <tr key={issuelog?.id}>
                {/* <td>{index + 1}</td> */}
                <td style={{ textAlign: "left" }}>{issuelog.date}</td>
                <td style={{ textAlign: "left" }}>{issuelog.warehouseName}</td>
                <td style={{ textAlign: "right" }}>{issuelog.voucherOrRefNumber}</td>
                <td style={{ textAlign: "left" }}>{issuelog.receivedFrom}</td>
                <td style={{ textAlign: "left" }}>{issuelog.issuedTo}</td>
                <td style={{ textAlign: "left" }}>{issuelog.batchNo}</td>
                <td style={{ textAlign: "left" }}>{issuelog.expiryDate}</td>
                <td style={{ textAlign: "right" }}>{issuelog.quantityReceived}</td>
                <td style={{ textAlign: "right" }}>{issuelog.quantityIssued}</td>
                {/* <td style={{ textAlign: "right" }}>{issuelog.lossesAndAdjustments}</td> */}
                <td style={{ textAlign: "right" }}>{issuelog.losses}</td>
                <td style={{ textAlign: "right" }}>{issuelog.positiveAdjustment}</td>
                <td style={{ textAlign: "right" }}>{issuelog.negativeAdjustment}</td>
                <td style={{ textAlign: "right" }}>{issuelog.stockBalance}</td>
              </tr>
            ))}
        </tbody>
      </table>

      <CSVLink
        headers={headers}
        data={data}
        filename="bin-card.csv"
        className="csv-link"
        ref={csvLinkEl}
        target="_blank"
      />
    </div>
  );
};

export default Bin;
