import React, { Component } from "react";
import InventoryService from "../services/InventoryService";

class UpdateInventories extends Component {
  constructor(props) {
    super(props);

    this.state = {
      id: this.props.match.params.id,
      date: "",
      warehouseName: "",
      itemDescription: "",
      batchNo: "",
      stockOnHand:"",
      q1: "",
      q2: "",
      q3: "",
      loading: false,
    };

    this.changeQuantityIssuedInMonthOneHandler = this.changeQuantityIssuedInMonthOneHandler.bind(this);
    this.changeQuantityIssuedInMonthTwoHandler = this.changeQuantityIssuedInMonthTwoHandler.bind(this);
    this.changeQuantityIssuedInMonthThreeHandler = this.changeQuantityIssuedInMonthThreeHandler.bind(this);
    this.updateInventory = this.updateInventory.bind(this)
  }

  componentDidMount() {
    InventoryService.getInventoryById(this.state.id).then((res) => {
      let inventory = res.data;
      this.setState({
        warehouseName: inventory.warehouseName,
        itemDescription: inventory.itemDescription,
        batchNo: inventory.batchNo,
        stockOnHand: inventory.stockOnHand,
        // q1: inventory.q1,
        // q2: inventory.q2,
        // q3: inventory.q3,
      });
    });
  }

  updateInventory = (e) => {
    e.preventDefault();
    this.setState({ loading: true });
  
    let inventory = {
      q1: this.state.q1,
      q2: this.state.q2,
      q3: this.state.q3,
    };
  
    if (this.state.q1) {
      if (this.state.q2) {
        if (this.state.q3) {
          InventoryService.updateInventorys(inventory, this.state.id)
            .then((res) => {
              this.props.history.push("/consolidated");
            })
            .catch((error) => {
              console.log(error);
            });
        } else {
          alert("Enter quantity Issued in month three");
          this.setState({ loading: false });
        }
      } else {
        alert("Enter quantity issued in month two!");
        this.setState({ loading: false });
      }
    } else {
      alert("Enter quantity issued in month one!");
      this.setState({ loading: false });
    }
  };

  changeQuantityIssuedInMonthOneHandler = (event) => {
    this.setState({ q1: event.target.value });
  };

  changeQuantityIssuedInMonthTwoHandler = (event) => {
    this.setState({ q2: event.target.value });
  };

  changeQuantityIssuedInMonthThreeHandler = (event) => {
    this.setState({ q3: event.target.value });
  };

  // changeSumHandler = (event) => {
  //   this.setState({ sum: event.target.value });
  // };

  // changeAmcHandler = (event) => {
  //   this.setState({ amc: event.target.value });
  // };


  // changeMosHandler = (event) => {
  //   this.setState({ mos: event.target.value });
  // };

  // changeRemarkHandler = (event) => {
  //   this.setState({ remark: event.target.value });
  // };

  cancel() {
    this.props.history.push("/consolidated");
  }

  render() {
    return (
      <React.Fragment>
        <div className="container" style={{ marginTop: "15px" }}>
          <div className="row">
            <div className="card col-md-8 offset-md-2 offset-md-2">
              <h3 className="text-center" style={{ marginTop: "25px", fontWeight: "bold" }}>Update Consolidated Stock</h3>
              <div className="card-body">
                <form>
                  <div className="container">
                    <div className="form-group">
                      <label style={{ marginLeft: "15px", fontWeight: "bold", fontSize: "15px" }}>Warehouse Name</label>
                      <div className="col-sm-12">
                        <input placeholder="Warehouse Name" name="warehouseName" className="form-control" value={this.state.warehouseName} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Item Description </label>
                      <div className="col-sm-12">
                        <input placeholder="Item Description" name="description" className="form-control" value={this.state.itemDescription} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Batch No.</label>
                      <div className="col-sm-12">
                        <input name="Batch No" className="form-control" value={this.state.batchNo} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Stock on hand</label>
                      <div className="col-sm-12">
                        <input name="Stock on hand" className="form-control" value={this.state.stockOnHand} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Quantity Issued in month one</label>
                      <div className="col-sm-12">
                        <input name="Quantity Issued in month one" type="number" className="form-control" value={this.state.q1} onChange={this.changeQuantityIssuedInMonthOneHandler} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Quantity Issued in month two</label>
                      <div className="col-sm-12">
                        <input name="Quantity Issued in month two" type="number" className="form-control" value={this.state.q2} onChange={this.changeQuantityIssuedInMonthTwoHandler} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Quantity Issued in month three</label>
                      <div className="col-sm-12">
                        <input name="Quantity Issued in month three" type="number" className="form-control" value={this.state.q3} onChange={this.changeQuantityIssuedInMonthThreeHandler} />
                      </div>

                    </div>

                    <div className="form-row text-center" style={{ marginTop: "12px" }} >
                      <div className="col-12">
                        <button className="btn btn-success" onClick={this.updateInventory} >
                          {this.state.loading && <div className="spinner-border text-light" role="status"></div>}
                          update </button>
                        <button className="btn btn-outline-danger" onClick={this.cancel.bind(this)} style={{ margin: "22px" }}>Cancel</button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
export default UpdateInventories;
