import React, { Component } from "react";
import "../App.css";
import { AccountBalanceOutlined, Visibility } from "@material-ui/icons";
import axios from "axios";
import { BaseURL } from "../services";

class BincardList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: JSON.parse(localStorage.getItem("user")).password,
      states: JSON.parse(localStorage.getItem("user")).states,
      userType: JSON.parse(localStorage.getItem("user")).userType,
      inventories: [],
      currentPage: 1,
      recordPerPage: 50,
      search: "",
      loading: false,
    };

    this.csvLinkEl = React.createRef();
    this.form = React.createRef();
  }

  componentDidMount() {
    this.getInventoriesByStatesPagination(this.state.currentPage);
  }

  getInventoriesByStatesPagination(currentPage) {
    currentPage = currentPage - 1;
    const userStates = this.state.userType === "User" ? encodeURIComponent(this.state.states) : "";
    axios
      .get(
        `${BaseURL}/inventories/${userStates}?page=${currentPage}&size=${this.state.recordPerPage}`
      )
      .then((response) => response.data)
      .then((data) => {
        this.setState({
          inventories: data.content,
          totalPages: data.totalPages,
          totalElements: data.totalElements,
          currentPage: data.number + 1,
        });
      });
  }

  showNextPage = () => {
    if (
      this.state.currentPage <
      Math.ceil(this.state.totalElements / this.state.recordPerPage)
    ) {
      if (!this.state.search) {
        this.getInventoriesByStatesPagination(this.state.currentPage + 1);
      } else {
        this.searchInventory(this.state.currentPage + 1);
      }
    }
  };

  showLastPage = () => {
    if (
      this.state.currentPage <
      Math.ceil(this.state.totalElements / this.state.recordPerPage)
    ) {
      if (!this.state.search) {
        this.getInventoriesByStatesPagination(
          Math.ceil(this.state.totalElements / this.state.recordPerPage)
        );
      } else {
        this.searchInventory(
          Math.ceil(this.state.totalElements / this.state.recordPerPage)
        );
      }
    }
  };

  showFirstPage = () => {
    let firstPage = 1;
    if (this.state.currentPage > firstPage) {
      if (!this.state.search) {
        this.getInventoriesByStatesPagination(firstPage);
      } else {
        this.searchInventory(firstPage);
      }
    }
  };

  showPrevPage = () => {
    let prevPage = 1;
    if (this.state.currentPage > prevPage) {
      if (!this.state.search) {
        this.getInventoriesByStatesPagination(
          this.state.currentPage - prevPage
        );
      } else {
        this.searchInventory(this.state.currentPage - prevPage);
      }
    }
  };

  handleChange = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  handleSearch = (event) => {
    event.preventDefault();
    this.searchInventory(this.state.search);
  };

  searchInventory = (search) => {
    if (search) {
      axios
        .get(
          `${BaseURL}/inventories/search/${search}?page=0&size=${this.state.recordPerPage}`
        )
        .then((response) => response.data)
        .then((data) => {
          this.setState({
            inventories: data.content,
            totalPages: data.totalPages,
            totalElements: data.totalElements,
            currentPage: data.number + 1,
          });
        });
    } else {
      this.getInventoriesByStatesPagination(this.state.currentPage);
    }
  };

  viewInventory(itemDescription) {
    try {
      const encodedItemDescription = encodeURIComponent(itemDescription);
      const decodedPathname = decodeURIComponent(
        `/bin-view/${encodedItemDescription}`
      );
      this.props.history.push(decodedPathname);
    } catch (e) {
      throw new URIError(
        `Pathname "${this.props.location.pathname
        }" could not be decoded. This is likely caused by an invalid percent-encoding.`
      );
    }
  }

  cancel = () => {
    this.props.history.push("/dashboard");
  };

  render() {
    const { inventories, userType, loading } = this.state;
    const data = userType === "User" ? inventories : this.state.inventories;
    const uniqueItemDescriptions = new Set(data.map((inventory) => inventory.itemDescription));

    return (
      <React.Fragment>

        <div className="asset-list">
          <div className="top" style={{ backgroundColor: "#008000" }}>
            <button
              style={{
                marginRight: "8px",
                marginTop: "10px",
                margin:"20px",
                backgroundColor: "#82E0AA",
                borderColor: "#82E0AA",
                color: "#D5F5E3",
              }}
              className="btn btn-primary float-lg-end"
              onClick={this.cancel}
              data-bs-toggle="tooltip"
              data-bs-placement="top"
              title="Back to Dashboard"
            >
              <AccountBalanceOutlined />
            </button>
            <div className="panel-heading">
              <span className="logs">Bin Cards</span>
            </div>
            <div className="panel-body">
              <form onSubmit={this.handleSearch}>
                <div className="form-group">
                  <div className="row">
                    <div className="col-md-3"></div>
                    <div className="col-md-3"></div>
                    <div className="col-md-6">
                      <div className="pull-right">

                      </div>
                    </div>
                  </div>
                </div>
              </form>
            </div>
          </div>
          {/* <div className="container"> */}
          <table className="table table-striped table-bordered">
            <thead style={{ textAlign: "center", fontSize: "12px" }}>
              <tr>
                <th>Warehouse Name</th>
                <th>Item Description</th>
                {/* <th>Batch No.</th> */}
                <th>Unit</th>
                <th>Date Received</th>
                {/* <th>Manufacture Date</th>
                <th>Expiry Date</th> */}
                <th>Stock state</th>
                <th>Quantity Received</th>
                <th>Quantity Issued</th>
                <th colSpan="4" style={{ backgroundColor: "wheat" }}>
                  Actions
                </th>
              </tr>
            </thead>
            <tbody style={{ textAlign: "center", fontSize: "11px" }}>
              {data.length === 0 ? (
                <tr align="center">
                  <td colSpan="20">No Record Found</td>
                </tr>
              ) : (
                Array.from(uniqueItemDescriptions).map((itemDescription, index) => {
                  const inventory = data.find((inventory) => inventory.itemDescription === itemDescription);
                  return (
                    <tr key={inventory?.id}>
                      <td style={{ textAlign: "left" }}>{inventory.warehouseName}</td>
                      <td style={{ textAlign: "left" }}>{inventory.itemDescription}</td>
                      {/* <td style={{ textAlign: "left" }}>{inventory.batchNo}</td> */}
                      <td style={{ textAlign: "left" }}>{inventory.unit}</td>
                      <td style={{ textAlign: "left" }}>{inventory.dateReceived}</td>
                      {/* <td style={{ textAlign: "left" }}>{inventory.manufactureDate}</td>
                      <td style={{ textAlign: "left" }}>{inventory.expiryDate}</td> */}
                      <td style={{ textAlign: "left" }}>{inventory.stockState}</td>
                      <td style={{ textAlign: "right" }}>{inventory.quantityReceived}</td>
                      <td style={{ textAlign: "right" }}>{inventory.quantityIssued}</td>
                      <td>
                        <button
                          className="btn btn-primary btn-xs"
                          onClick={() => this.viewInventory(inventory.itemDescription)}
                          data-bs-toggle="tooltip"
                          data-bs-placement="top"
                          title="View bin card"
                        >
                          View
                        </button>
                      </td>
                    </tr>
                  );
                })
              )}
            </tbody>
          </table>
        </div>
        <div className="panel-footer">
          <div className="row">
            <div className="col-md-6">
              <button className="btn btn-primary" onClick={this.showFirstPage}>
                First
              </button>
              <button className="btn btn-primary ml-2" onClick={this.showPrevPage}>
                Previous
              </button>
              <button className="btn btn-primary ml-2" onClick={this.showNextPage}>
                Next
              </button>
              <button className="btn btn-primary ml-2" onClick={this.showLastPage}>
                Last
              </button>
            </div>
            <div className="col-md-6">
              <div className="pull-right">
                Showing Page {this.state.currentPage} of {Math.ceil(this.state.totalElements / this.state.recordPerPage)}
              </div>
            </div>
          </div>
          {/* </div> */}
        </div>

      </React.Fragment>
    );
  }
}

export default BincardList;
