import React, { Component } from "react";
import '../App.css'
import { Link } from 'react-router-dom';
import { Visibility, Delete, CloudUploadOutlined, CloudDownloadOutlined, AddCircleOutlineSharp, AccountBalanceOutlined, SearchOutlined, UpdateOutlined, Edit, Add } from '@material-ui/icons'
import { CSVLink } from "react-csv";
import axios from 'axios'
import { BaseURL } from "../services";
import InventoryService from "../services/InventoryService";
import moment from 'moment';


const headers = [
  // { label: "Id", key: "id" },
  { label: "Warehouse Name", key: "warehouseName" },
  { label: "Date Received", key: "dateReceived" },
  { label: "Item Description", key: "itemDescription" },
  { label: "Batch No.", key: "batchNo" },
  { label: "Expiry date", key: "expiryDate" },
  { label: "shelf Life (Months)", key: "shellLife" },
  { label: "Month of Stock", key: "mos" },
  { label: "Donor", key: "donor" }

];


class Report extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: JSON.parse(localStorage.getItem('user')).password,
      states: JSON.parse(localStorage.getItem('user')).states,
      userType: JSON.parse(localStorage.getItem('user')).userType,
      inventories: [],
      currentPage: 1,
      recordPerPage: 100,
      search: '',
      startDate: '',
      endDate: '',
      loading: false
    };

    this.csvLinkEl = React.createRef();
    this.form = React.createRef();

    // this.createInventory = this.createInventory.bind(this);
    // this.editInventory = this.editInventory.bind(this);
    // this.deleteInventory = this.deleteInventory.bind(this);
    // this.viewInventory = this.viewInventory.bind(this);
  }

  componentDidMount() {
    this.getInventoriesByStatesPagination(this.state.currentPage);
  }


  getInventoriesByStatesPagination(currentPage) {
    currentPage = currentPage - 1;
    axios.get(`${BaseURL}/inventories/${this.state.userType === 'User' ? this.state.states : ''}?page=${currentPage}&size=${this.state.recordPerPage}`)
      .then(response => response.data).then((data) => {
        this.setState({
          inventories: data.content,
          totalPages: data.totalPages,
          totalElements: data.totalElements,
          currentPage: data.number + 1
        });
      });
  }


  //Writing All the pagination functions
  //Show Next page
  showNextPage = () => {
    if (this.state.currentPage < Math.ceil(this.state.totalElements / this.state.recordPerPage)) {
      if (!this.state.search) {
        this.getInventoriesByStatesPagination(this.state.currentPage + 1);
      } else {
        this.searchInventory(this.state.currentPage + 1)
      }
    }
  };

  //Show Last Page
  showLastPage = () => {
    if (this.state.currentPage < Math.ceil(this.state.totalElements / this.state.recordPerPage)) {
      if (!this.state.search) {
        this.getInventoriesByStatesPagination(Math.ceil(this.state.totalElements / this.state.recordPerPage));
      }
      else {
        this.searchInventory(Math.ceil(this.state.totalElements / this.state.recordPerPage));
      }
    }
  };

  //Show First page
  showFirstPage = () => {
    let firstPage = 1;
    if (this.state.currentPage > firstPage) {
      if (!this.state.search) {
        this.getInventoriesByStatesPagination(firstPage);
      } else {
        this.searchInventory(firstPage)
      }
    }
  };

  //Show previous page
  showPrevPage = () => {
    let prevPage = 1
    if (this.state.currentPage > prevPage) {
      if (!this.state.search) {
        this.getInventoriesByStatesPagination(this.state.currentPage - prevPage);
      } else {
        this.searchInventory(this.state.currentPage - prevPage);
      }
    }
  };

  //Search Box Method
  searchBox = (e) => {
    this.setState({
      //assigning value to event target
      [e.target.name]: e.target.value,
    });
  };

  //Search Method Logic
  searchInventory = (currentPage) => {
    currentPage = currentPage - 1;
    axios.get(`${BaseURL}/inventory/` + this.state.search + "?page=" + currentPage + "&size=" + this.state.recordPerPage)
      .then(response => response.data).then((data) => {
        this.setState({
          inventories: data.content,
          totalPages: data.totalPages,
          totalElements: data.totalElements,
          currentPage: data.number + 1
        });
      });
  };
  //Reset Search Box
  resetInventory = (currentPage) => {
    this.setState({ "search": '' });
    this.getInventoriesByStatesPagination(currentPage);
  };

  // deleteInventory(id) {
  //   this.setState({ open: true, inventoryId: id })
  // }

  deleteInventory(id) {
    const text = 'Are you sure you want to delete?'
    if (window.confirm(text) === true) {
      InventoryService.deleteInventory(id).then((res) => {
        this.setState({
          inventories: this.state.inventories.filter((inventory) => inventory.id !== id),
        });
      });
    }
  }


  handleSearch = () => {
    const { startDate, endDate, currentPage, recordPerPage } = this.state;
    const formattedStartDate = this.formatDate(startDate);
    const formattedEndDate = this.formatDate(endDate);

    axios.get(`${BaseURL}/inventory/search?startDate=${formattedStartDate}&endDate=${formattedEndDate}&page=${currentPage}&size=${recordPerPage}`)
      .then(response => {
        const data = response.data;
        this.setState({
          inventories: data.content,
          totalPages: data.totalPages,
          totalElements: data.totalElements,
          currentPage: data.number + 1,
          searchResultEmpty: data.content.length === 0, // Set searchResultEmpty state
        });
      })
      .catch(error => {
        console.error('Error:', error);
      });
  };


  formatDate = (date) => {
    const formattedDate = new Date(date).toLocaleDateString('en-GB', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric'
    });
    return formattedDate;
  };

  // handleExport = () => {
  //   const { startDate, endDate, currentPage } = this.state;
  //   const formattedStartDate = this.formatDate(startDate);
  //   const formattedEndDate = this.formatDate(endDate);
  //   axios.get(`http://localhost:8080/api/v1/inventory/exports?startDate=${formattedStartDate}&endDate=${formattedEndDate}&page=${currentPage}&size=${this.state.recordPerPage}`)
  //     .then(response => {
  //       const csvData = response.data;

  //       const csvFile = new Blob([csvData], { type: 'text/csv' });
  //       const csvURL = URL.createObjectURL(csvFile);
  //       const tempLink = document.createElement('a');
  //       tempLink.href = csvURL;
  //       tempLink.setAttribute('download', 'stock_report.csv');
  //       tempLink.click();
  //     })
  //     .catch(error => {
  //       console.error('Error:', error);
  //     });
  // };



  handleExport = () => {
    const { startDate, endDate, currentPage, recordPerPage } = this.state;
    const formattedStartDate = this.formatDate(startDate);
    const formattedEndDate = this.formatDate(endDate);

    const params = new URLSearchParams();
    params.append('startDate', formattedStartDate);
    params.append('endDate', formattedEndDate);
    params.append('page', currentPage);
    params.append('size', recordPerPage);

    axios
      .get(`http://localhost:8080/api/v1/export?${params.toString()}`, { responseType: 'blob' }) // Add responseType: 'blob'
      .then(response => {
        const csvData = response.data;
        const blob = new Blob([csvData], { type: 'text/csv' });
        const csvURL = URL.createObjectURL(blob);
        const tempLink = document.createElement('a');
        tempLink.href = csvURL;
        tempLink.setAttribute('download', 'stock_report.csv');
        tempLink.click();
      })
      .catch(error => {
        console.error('Error:', error);
      });
  };


  handlePageChange = page => {
    this.setState({ currentPage: page }, () => {
      this.handleSearch();
    });
  };


  cancel() {
    this.props.history.push("/dashboard");
  }

  upload() {
    this.props.history.push("/upload-inventory");
  }

  render() {
    const { inventories, currentPage, totalPages, recordPerPage, search, startDate, endDate, } = this.state;
    const user = JSON.parse(localStorage.getItem('user'));
    const users = JSON.parse(localStorage.getItem('user'))?.userType;
    const userType = user?.userType;
    const userLocation = user?.result?.states
    const userInventories = this.state?.inventories?.map((x) => x).filter((x) => x.states === userLocation)
    const data = userType !== 'User' ? this.state.inventories : userInventories;

    const downloadReport = async () => {
      this.setState({ data: data }, () => {
        setTimeout(() => {
          this.csvLinkEl.current.link.click();
        });
      });
    }

    return (
      <React.Fragment>

        <div className="asset-list">
          {/* <Topbar /> */}
          {/* <div className="row"> */}
          <div className="top" style={{ backgroundColor: "#008000" }}>
            <div style={{ margin: "20px" }} >
            <button style={{ margin: "3px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" onClick={this.cancel.bind(this)} data-bs-toggle="tooltip" data-bs-placement="top" title="Home">
                <AccountBalanceOutlined />
              </button>
              
            </div>
            <div className="d-flex flex-row bd-highlight mb-3">
            <span className="logs"style={{marginTop:"23px"}}>Stock Status Report</span>
            </div>
            <div className="topRight">
              <span style={{ backgroundColor: "#82E0AA", padding: "4px",  color: "#FFFFFFF", margin: "12px" }}>Start Date</span>
              <input style={{ borderRadius: "6px", marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" type="date" value={startDate} data-bs-placement="top" title="start date" onChange={e => this.setState({ startDate: e.target.value })} />
              <span style={{ backgroundColor: "#82E0AA", padding: "4px", color: "#FFFFFFF", margin: "12px" }}>End Date</span>
              <input style={{ borderRadius: "6px", marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" type="date" value={endDate} data-bs-placement="top" title="end date" onChange={e => this.setState({ endDate: e.target.value })} />
              <button style={{ borderRadius: "6px", marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" data-bs-placement="top" title="search by dates" onClick={this.handleSearch}> <SearchOutlined /></button>

              {/* {
                users !== 'User' &&
                <button style={{ marginRight: "8px", backgroundColor: "#82E0AA", borderColor: "#82E0AA", color: "#D5F5E3" }} className="btn btn-primary float-lg-end" onClick={downloadReport} data-bs-toggle="tooltip" data-bs-placement="top" title="Download">
                  <CloudDownloadOutlined />
                </button>
              } */}
            </div>
          </div>
          <table className="table table-striped table-bordered">
            <thead style={{ textAlign: "center", fontSize: "12px" }}>
              <tr>
                <th>No.</th>
                <th>Warehouse Name</th>
                <th>Date Received</th>
                <th>Item Description</th>
                <th>Batch/Lot/Serial No.</th>
                <th>Expiry Date</th>
                <th>Shelf Life (Months)</th>
                <th>Stock Balance</th>
                <th>Month Of Stock</th>
                <th>Donor</th>
                {/* <th colSpan="4">Actions</th> */}
              </tr>
            </thead>
            <tbody style={{ textAlign: "center", fontSize: "11px" }}>
              {inventories.length === 0 ?
                <tr align="center"><td colSpan="20">No Record Found</td></tr> :
                data?.map((inventory, index) => (
                  <tr key={inventory?.id}
                    style={{
                      backgroundColor:
                        this.isDateLessThanSixMonths(inventory.expiryDate) ? '#ED7D64' : 'inherit'
                    }}>
                    <td>
                      {(recordPerPage * (currentPage - 1)) + index + 1}</td>
                    <td style={{ textAlign: "left" }}>{inventory.warehouseName}</td>
                    <td style={{ textAlign: "left" }}>{inventory.dateReceived}</td>
                    <td style={{ textAlign: "left" }}>{inventory.itemDescription}</td>
                    <td style={{ textAlign: "left" }}>{inventory.batchNo}</td>
                    <td style={{ textAlign: "left" }}>{inventory.expiryDate}</td>
                    <td style={{ textAlign: "right" }}>{inventory.shellLife}</td>
                    <td style={{ textAlign: "right" }}>{inventory.stockBalance}</td>
                    <td style={{ textAlign: "right" }}>{inventory.mos}</td>
                    <td style={{ textAlign: "left" }}>{inventory.donor}</td>

                    {/* <td className="text-center" style={{  }}><Link to={`/update-inventory/${inventory.id}`} className="create bin card" data-bs-toggle="tooltip" data-bs-placement="top" title="Create bin card"><Add /></Link></td>

                    <td className="text-center" style={{ paddingLeft: "20px" }}><Link to={`/update-invent/${inventory.id}`} data-bs-toggle="tooltip" data-bs-placement="top" title="edit" className="edit"><Edit /></Link></td>
                    {
                      users !== 'User' &&
                      <td className="text-center" style={{ paddingLeft: "20px" }}><i onClick={() => this.deleteInventory(inventory.id)} className="fa fa-trash" style={{ color: "red" }} data-bs-toggle="tooltip" data-bs-placement="top" title="Delete"><Delete /> </i></td>
                    }
                    <td className="text-center" style={{ paddingLeft: "20px" }}><Link to={`/view-inventory/${inventory.id} `} className="view" style={{ alignItem: "center", color: "green" }} data-bs-toggle="tooltip" data-bs-placement="top" title="View"> <Visibility /></Link> </td> */}
                  </tr>
                ))}
            </tbody>
          </table>
          <table className="table">
            <div style={{ float: 'left', fontFamily: 'monospace', color: '#0275d8' }}>
              Page {currentPage} of {totalPages}
            </div>
            <div style={{ float: 'right' }}>
              <div className="clearfix"></div>
              <nav aria-label="Page navigation example">
                <ul className="pagination">
                  <li className="page-item"><a type="button" className="page-link" disabled={currentPage === 1 ? true : false} onClick={this.showPrevPage}>Previous</a></li>
                  <li className="page-item"><a type="button" className="page-link" disabled={currentPage === 1 ? true : false} onClick={this.showFirstPage}>First</a></li>
                  <li className="page-item"><a type="button" className="page-link" disabled={currentPage === totalPages ? true : false} onClick={this.showNextPage}>Next</a></li>
                  <li className="page-item"><a type="button" className="page-link" disabled={currentPage === totalPages ? true : false} onClick={this.showLastPage}>Last</a></li>
                </ul>
              </nav>
            </div>
          </table>

          <button style={{ color: "#000000", margin: "18px", backgroundColor: "#82E0AA", borderColor: "#82E0AA",  }} className="btn btn-primary float-lg-end" data-bs-placement="top" title="export by dates" onClick={this.handleExport}>Export to CSV</button>
          <CSVLink
            headers={headers}
            data={data}
            totalPages={totalPages}
            filename="stock_status.csv"
            target="_blank"
            ref={this.csvLinkEl} />
        </div>
      </React.Fragment>
    )
  }
  isDateLessThanSixMonths(dateString) {
    const moment = require('moment');

    // Create a new date object for the current date
    const expiryDate = new Date();

    // Parse the dateString parameter into a Date object
    const date = new Date(dateString);

    // Format the currentDate using moment.js
    const formattedDate = moment(expiryDate).format('DD MMM YYYY');

    // Create a new date object for six months ago
    const sixMonthsAgo = new Date();
    sixMonthsAgo.setMonth(expiryDate.getMonth() + 6);

    console.log("Dates ::: " + sixMonthsAgo);


    // // Compare the parsed date with the date six months ago
    return date <= sixMonthsAgo;

  }
}
export default Report;
