
import React, { Component } from "react";
import IssueLogService from "../services/IssueLogService";

class ViewIssueLog extends Component {
    constructor(props) {
        super(props);

        this.state = {
            id: this.props.match.params.id,
            issuelogs: {
            },
        };
    }

    componentDidMount() {
        IssueLogService.getIssueLogById(this.state.id).then((res) => {
            this.setState({ issuelogs: res.data });

        });
    }

    print() {
        window.print();
    }
    cancel() {
        this.props.history.push("/issue-logs");
    }

    render() {

        return (
            <div>
                <div className="container" style={{ marginTop: "15px" }}>
                    <div className="row">
                        <div className="card col-md-8 offset-md-2 offset-md-2">
                            <h3 className="text-center" style={{ margin: "15px", fontWeight: "bold" }}>Detail</h3>
                            <div className="card-body" style={{ marginLeft: "15px", marginRight: "15px", fontFamily: "cursive" }}>
                                <table class="table" >
                                    <tbody>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Date :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.date}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Warehouse name :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.warehouseName}</td>
                                        </tr>

                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Voucher Or Ref. No.:</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.voucherOrRefNumber}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Item Description :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.itemDescription}</td>
                                        </tr>
                                        <tr >
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Batch No :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.batchNo}</td>
                                        </tr>

                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Expiry Date :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.expiryDate}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Unit :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.unit}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Received from:</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.receivedFrom}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Opening Balance :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.openingBalance}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Quantity Received :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.quantityReceived}</td>
                                        </tr>

                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Quantity Received :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.quantityIssued}</td>
                                        </tr>


                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Closing Stock :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.closingStock}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Issued To:</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.issuedTo}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Phone :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.phone}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>issuedToEmail :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.issuedToEmail}</td>
                                        </tr>

                                        <tr>
                                            <th scope="row"></th>
                                            <td style={{ fontSize: "20px", fontWeight: "bold" }}>Dispatched Location :</td>
                                            <td style={{ fontSize: "20px" }}>{this.state.issuelogs.dispatchedLocation}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <br></br>
                            <br></br>
                            <div className="text-center">
                                <button className="btn btn-outline-danger" onClick={this.cancel.bind(this)} style={{ marginBottom: "22px", alignSelf: "center" }}>Cancel</button>
                                <button className="btn  btn-primary" onClick={this.print} style={{ marginBottom: "22px", marginLeft: "22px", alignSelf: "center" }}>Print</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ViewIssueLog;
