import React, { Component } from "react";
import InventoryService from "../services/InventoryService";


class UpdateInvent extends Component {
  constructor(props) {
    super(props);

    this.state = {
      id: this.props.match.params.id,
      // date: "",
      warehouseName: "",
      itemDescription: "",
      batchNo: "",
      manufactureDate: "",
      expiryDate: "",
      // openingBalance: "",
      stockBalance:"",
      closingStock: "",
      stockOnHand: "",
      reportingMonth: "",
      losses:"",
      positiveAdjustment:"",
      negativeAdjustment:"",
      donor: "",
      quantityReceived:"",
      remark:"",
      loading: false,

    };

  
    // this.changeDateHandler = this.changeDateHandler.bind(this);
    this.changeManufactureDateHandler = this.changeManufactureDateHandler.bind(this);
    this.changeOpeningBalanceHandler = this.changeOpeningBalanceHandler.bind(this);
    this.changeClosingStockHandler = this.changeClosingStockHandler.bind(this);
    this.changeStockOnHandHandler = this.changeStockOnHandHandler.bind(this);
    this.changeReportingMonthHandler = this.changeReportingMonthHandler.bind(this);
    this.changeExpiryDateHandler = this.changeExpiryDateHandler.bind(this);
    this.changeQuantityReceivedHandler = this.changeQuantityReceivedHandler.bind(this);
    this.changeLossesHandler = this.changeLossesHandler.bind(this);
    this.changePositiveAdjustmentHandler =this.changePositiveAdjustmentHandler.bind(this);
    this.changeNegativeAdjustmentHandler = this.changeNegativeAdjustmentHandler.bind(this);
    this.changeRemarkHandler = this.changeRemarkHandler.bind(this);

    this.updateInventory = this.updateInventory.bind(this)
  }

  componentDidMount() {
    InventoryService.getInventoryById(this.state.id).then((res) => {
      let inventory = res.data;
      this.setState({
        warehouseName: inventory.warehouseName,
        batchNo: inventory.batchNo,
        itemDescription: inventory.itemDescription,
        manufactureDate: inventory.manufactureDate,
        expiryDate: inventory.expiryDate,
        // openingBalance: inventory.openingBalance,
        stockBalance:inventory.stockBalance,
        closingStock: inventory.closingStock,
        quantityReceived: inventory.quantityReceived,
        stockOnHand: inventory.stockOnHand,
        reportingMonth: inventory.reportingMonth,      

      });
    });
  }

  updateInventory = (e) => {
    e.preventDefault();
    this.setState({ loading: true })

    let inventory = {
      // date: new Date().toLocaleString().slice(0, 10),
      manufactureDate: this.state.manufactureDate,
      openingBalance: this.state.openingBalance,
      closingStock: this.state.closingStock,
      reportingMonth: this.state.reportingMonth,
      stockOnHand: this.state.stockOnHand,
      quantityReceived: this.state.quantityReceived,
      losses:this.state.losses,
      positiveAdjustment: this.state.positiveAdjustment,
      negativeAdjustment:this.state.negativeAdjustment,
      remark:this.state.remark,
    };
  
    InventoryService.updateInvent(inventory, this.state.id).then((res) => {
      this.props.history.push("/inventories");
    });

  };

  changeManufactureDateHandler = (event) => {
    this.setState({ manufactureDate: event.target.value });
  };

  changeLossesHandler = (event) => {
    this.setState({ losses: event.target.value });
  };

  changeOpeningBalanceHandler = (event) => {
    this.setState({ openingBalance: event.target.value });
  };

  changeClosingStockHandler = (event) => {
    this.setState({ closingStock: event.target.value });
  };

  changeStockOnHandHandler = (event) => {
    this.setState({ stockOnHand: event.target.value });
  };

  changeReportingMonthHandler = (event) => {
    this.setState({ reportingMonth: event.target.value });
  };


  changeRemarkHandler = (event) => {
    this.setState({ remark: event.target.value });
  };


  changeExpiryDateHandler = (event) => {
    this.setState({ expiryDate: event.target.value });
  };

  changeQuantityReceivedHandler = (event) => {
    this.setState({ quantityReceived: event.target.value });
  };
  
  changeLossAndAdjustmentHandler = (event) => {
    this.setState({ lossesAndAdjustments: event.target.value });
  };

  changePositiveAdjustmentHandler =(event)=>{
    this.setState({positiveAdjustment: event.target.value});
  }

  changeNegativeAdjustmentHandler = (event) =>{
    this.setState({negativeAdjustment:event.target.value});
  }
  
  

  cancel() {
    this.props.history.push("/inventories");
  }

  render() {
    return (
      <React.Fragment>
        <div className="container" style={{ marginTop: "15px", padding: "50px" }}>
          <div className="row">
            <div className="card col-md-8 offset-md-2 offset-md-2">
              <h3 className="text-center" style={{ marginTop: "25px", fontWeight: "bold" }}>Update health commodity</h3>
              <div className="card-body">
                <form>
                  <div className="container">
                    <div className="form-group">
                      <label style={{ marginLeft: "15px", fontWeight: "bold", fontSize: "15px" }}>Warehouse Name</label>
                      <div className="col-sm-12">
                        <input placeholder="Warehouse Name" name="warehouseName" className="form-control" value={this.state.warehouseName} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Item Description </label>
                      <div className="col-sm-12">
                        <input placeholder="Item Description" name="itemDescription" className="form-control" value={this.state.itemDescription} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Batch No</label>
                      <div className="col-sm-12">
                        <input name="batchNo" className="form-control" value={this.state.batchNo} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Manufacture Date</label>
                      <div className="col-sm-12">
                        <input name="manufactureDate" type="date" className="form-control" value={this.state.manufactureDate}  />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Expiry Date</label>
                      <div className="col-sm-12">
                        <input name="expiryDate"  type="date" className="form-control" value={this.state.expiryDate}  />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Stock Balance</label>
                      <div className="col-sm-12">
                        <input name="stockBalance" className="form-control" value={this.state.stockBalance}  />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Quantity Received</label>
                      <div className="col-sm-12">
                        <input name="stockBalance" className="form-control" value={this.state.quantityReceived}  />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Closing Stock</label>
                      <div className="col-sm-12">
                        <input name="closingStock" className="form-control" value={this.state.closingStock}  />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Closing Stock</label>
                      <div className="col-sm-12">
                        <input name="closingStock" className="form-control" value={this.state.closingStock} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Losses</label>
                      <div className="col-sm-12">
                        <input name="losses"type="number" className="form-control" value={this.state.losses} onChange={this.changeLossesHandler} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Positve Adjustment</label>
                      <div className="col-sm-12">
                        <input name="positiveAdjustment"type="number" className="form-control" value={this.state.positiveAdjustment} onChange={this.changePositiveAdjustmentHandler} />
                      </div>

                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Negative Adjustment</label>
                      <div className="col-sm-12">
                        <input name="negativeAdjustment"type="number" className="form-control" value={this.state.negativeAdjustment} onChange={this.changeNegativeAdjustmentHandler} />
                      </div>

                      
                      <label style={{ marginTop: "10px", marginLeft: "15px", fontWeight: "bold" }}>Remark</label>
                      <div className="col-sm-12">
                        <textarea className="form-control" value={this.state.remark} onChange={this.changeRemarkHandler} />
                      </div>

                    </div>

                    <div className="form-row text-center" style={{ marginTop: "12px" }} >
                      <div className="col-12">
                        <button className="btn btn-success" onClick={this.updateInventory} >
                          {this.state.loading && <div className="spinner-border text-light" role="status"></div>}
                          Update health commodity</button>
                        <button className="btn btn-outline-danger" onClick={this.cancel.bind(this)} style={{ margin: "22px" }}>Cancel</button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}
export default UpdateInvent;
