import axios from "axios";

import { BaseURL } from "./index";

export const GetIssueLogs = async () => {
  try {
    const { data } = await axios.get(`${BaseURL}/issuelogs`)
    return data
  } catch (error) {
    return error.message
  }
}

class IssueLogService {
  getIssueLogs() {
    return axios.get(`${BaseURL}/issuelogs`);
  }

  getAllIssueLogById(issuelogId) {
    return axios.get(`${BaseURL}/issuelog/` + issuelogId)
  }

  createIssueLog(issuelog) {
    return axios.post(`${BaseURL}/issuelog`, issuelog);
  }

  getIssueLogById(issuelogId) {
    return axios.get(`${BaseURL}/issuelog/` + issuelogId);
  }

  getIssueLogByInventoryId(inventoryId) {
    return axios.get(`${BaseURL}/issuelog/inventory` + inventoryId);
  }
}

export default new IssueLogService();